<template>
  <div class="preview align-self-center">
    <h1 class="text-center"></h1>
    <canvas class="rounded mx-auto d-block w-100" ref="canvasView">
      <!--  canvas -->
       HTML5 ()
    </canvas>
    <p class="text-danger">
      (font)
    </p>
  </div>
</template>

<script>
export default {
  name: "ArticlePreview",
  props: {
    /**
     * 
     */
    content: {
      type: String,
      required: true,
    },
    /**
     * 
     */
    theme: {
      type: String,
      required: true,
    },
    /**
     * 
     */
    font: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      /**
       * 
       */
      canvas: {
        view: null,
        ctx: null,
        width: 960,
        height: 720,
        center: false,
      },
      /**
       * 
       */
      default: {
        canvas: {
          width: 960,
          height: 720,
        },
        line: {
          height: 80,
        },
      },
      /**
       * 
       */
      themes: {
        "black-green": {
          background: "#000000",
          text: "#00FF3B",
        },
        "black-yellow": {
          background: "#000000",
          text: "#EBD443",
        },
        "black-white": {
          background: "#000000",
          text: "#F8F9FA",
        },
        "black-red": {
          background: "#000000",
          text: "#DC3545",
        },
        "sweet-panda": {
          background: "#E83E8C",
          text: "#F8F9FA",
        },
        "blue-white": {
          background: "#007BFF",
          text: "#F8F9FA",
        },
        "white-blue": {
          background: "#F8F9FA",
          text: "#007BFF",
        },
        laravel: {
          background: "#F4645F",
          text: "#F8F9FA",
        },
        "soft-green": {
          background: "#39C5BB",
          text: "#000000",
        },
        "grey-pikachu": {
          background: "#2F3437",
          text: "#FFD547",
        },
        "grey-eevee": {
          background: "#2F3437",
          text: "#E7AF56",
        },
        "pikachu-grey": {
          background: "#FFD547",
          text: "#2F3437",
        },
        "eevee-grey": {
          background: "#E7AF56",
          text: "#2F3437",
        },
        "chinese-new-year": {
          background: "#A61723",
          text: "#D8B06A",
        },
        "reverse-chinese-new-year": {
          background: "#D8B06A",
          text: "#A61723",
        },
        devotion: {
          background: "#F11541",
          text: "#000000",
        },
        "windows-10-error": {
          background: "#007BD0",
          text: "#F8F9FA",
        },
        "windows-10-error-testing": {
          background: "#107C10",
          text: "#F8F9FA",
        },
        pink: {
          background: "#F8C0C8",
          text: "#FF5376",
        },
        "broken-think": {
          background: "#F8F9FA",
          text: "#000000",
        },
        "furry-broken-think": {
          background: "#F8F9FA",
          text: "#000000",
        },
      },
      /**
       * 
       */
      fonts: {
        auraka: {
          name: "AURAKA ",
          font: "Auraka",
        },
        kc24m: {
          name: "",
          font: "KC24M",
        },
        "microsoft-jheng-hei": {
          name: "",
          font: "Microsoft JhengHei",
        },
        mingliu: {
          name: "",
          font: "Mingliu",
        },
        kaiu: {
          name: "",
          font: "Kaiu",
        },
        "fot-matissepro-eb": {
          name: "",
          font: "MatissePro EB",
        },
        "taipei-sans-tc-beta-bold": {
          name: "",
          font: "Taipei Sans TC Beta",
        },
        "cubic-11": {
          name: " 11 ",
          font: "Cubic 11",
        },
      },
    };
  },
  mounted() {
    this.resetCanvasView();
    this.settingCanvasViewSize();
    this.drawingBackground();
    this.drawingBackgroundImage();
    this.drawingLogo();
    this.drawingUrl();
    this.drawingContent();
  },
  methods: {
    /**
     *  Canvas view ctx 
     *
     * @return void
     */
    resetCanvasView() {
      this.canvas.view = this.$refs.canvasView;
      this.canvas.ctx = this.canvas.view.getContext("2d");
    },
    /**
     *  Canvas view (width)(height)
     *
     * @return void
     */
    settingCanvasViewSize() {
      /**
       * 
       */
      let lineCount = this.contentSplit().length;
      /**
       *  ${default.line.height}  600px 
       */
      let canvasViewCenter =
        lineCount * this.default.line.height < 600 ? true : false;
      /**
       *  Canvas (width)(height)
       *  ${default.canvas.height}
       *  + ( * ${default.line.height})
       */
      let canvasViewWidth = this.default.canvas.width;
      let canvasViewHeight = canvasViewCenter
        ? this.default.canvas.height
        : 144 + lineCount * this.default.line.height;
      /**
       * (width)(height)
       */
      switch (this.theme) {
        // : Windows 10 
        case "windows-10-error":
          canvasViewHeight += 360;
          break;

        // : Windows 10  
        case "windows-10-error-testing":
          canvasViewHeight += 360;
          break;

        // : 
        case "broken-think":
          canvasViewHeight += 160;
          canvasViewWidth += 349;
          break;

        // : 
        case "furry-broken-think":
          canvasViewHeight += 160;
          canvasViewWidth += 349;
          break;
      }
      /**
       *  data() 
       */
      this.canvas.width = canvasViewWidth;
      this.canvas.height = canvasViewHeight;
      this.canvas.center = canvasViewCenter;
      /**
       *  canvas (width)(height)
       */
      this.canvas.view.width = this.canvas.width;
      this.canvas.view.height = this.canvas.height;
    },
    /**
     * 
     *
     * @return void
     */
    drawingBackground() {
      this.canvas.ctx.fillStyle = this.themes[this.theme].background;
      this.canvas.ctx.fillRect(0, 0, this.canvas.width, this.canvas.height);
    },
    /**
     * 
     *
     * @return void
     */
    drawingBackgroundImage() {
      let self = this;
      switch (this.theme) {
        // : 
        case "devotion":
          var sources = {
            devotion: "/img/frontend/cards/devotion-bg.png",
          };
          this.loadingImages(sources, function (images) {
            self.canvas.ctx.drawImage(images.devotion, 360, 64);
            self.drawingLogo();
            self.drawingUrl();
          });
          break;

        // : Windows 10 
        case "windows-10-error":
          var sources = {
            qrcode: "/img/frontend/cards/qrcode.png",
          };
          this.loadingImages(sources, function (images) {
            self.canvas.ctx.drawImage(
              images.qrcode,
              24,
              self.canvas.height - 204
            );
            self.drawingLogo();
            self.drawingUrl();
          });
          break;

        // : Windows 10  
        case "windows-10-error-testing":
          var sources = {
            qrcode: "/img/frontend/cards/qrcode.png",
          };
          this.loadingImages(sources, function (images) {
            self.canvas.ctx.drawImage(
              images.qrcode,
              24,
              self.canvas.height - 204
            );
            self.drawingLogo();
            self.drawingUrl();
          });
          break;

        // : 
        case "broken-think":
          /**
           * 
           * background => 
           * people => 
           * arrow => 
           */
          var sources = {
            background: "/img/frontend/cards/fragmented_background.png",
            people: "/img/frontend/cards/fragmented_people.png",
            arrow: "/img/frontend/cards/fragmented_background_arrow.png",
          };
          this.loadingImages(sources, function (images) {
            /**
             * 
             */
            self.canvas.ctx.drawImage(
              images.background,
              0,
              self.canvas.height - 560
            );
            self.canvas.ctx.drawImage(
              images.people,
              36,
              self.canvas.height - 542
            );

            /**
             * 
             */
            self.canvas.ctx.lineJoin = "round";
            self.canvas.ctx.lineWidth = 8;
            self.canvas.ctx.strokeRect(
              353,
              40,
              self.canvas.width - 381,
              self.canvas.height - 282
            );
            self.canvas.ctx.fillStyle = "#FFFFFF";
            self.canvas.ctx.fillRect(
              357,
              44,
              self.canvas.width - 389,
              self.canvas.height - 290
            );

            /**
             * 
             */
            self.canvas.ctx.drawImage(
              images.arrow,
              312,
              self.canvas.height - 388
            );
            self.drawingLogo();
            self.drawingUrl();
            self.drawingContent();
          });
          break;

        // : 
        case "furry-broken-think":
          /**
           * 
           * background => 
           * wolf => 
           * arrow => 
           */
          var sources = {
            background: "/img/frontend/cards/fragmented_background.png",
            wolf: "/img/frontend/cards/fragmented_wolf.png",
            arrow: "/img/frontend/cards/fragmented_background_arrow.png",
          };
          this.loadingImages(sources, function (images) {
            /**
             * 
             */
            self.canvas.ctx.drawImage(
              images.background,
              0,
              self.canvas.height - 560
            );
            self.canvas.ctx.drawImage(
              images.wolf,
              12,
              self.canvas.height - 482
            );

            /**
             * 
             */
            self.canvas.ctx.lineJoin = "round";
            self.canvas.ctx.lineWidth = 8;
            self.canvas.ctx.strokeRect(
              353,
              40,
              self.canvas.width - 381,
              self.canvas.height - 282
            );
            self.canvas.ctx.fillStyle = "#FFFFFF";
            self.canvas.ctx.fillRect(
              357,
              44,
              self.canvas.width - 389,
              self.canvas.height - 290
            );

            /**
             * 
             */
            self.canvas.ctx.drawImage(
              images.arrow,
              312,
              self.canvas.height - 388
            );
            self.drawingLogo();
            self.drawingUrl();
            self.drawingContent();
            // self.drawingFeature();
          });
          break;
      }
    },
    /**
     *  LOGO
     *
     * @return void
     */
    drawingLogo() {
      switch (this.theme) {
        // : Windows 10 
        case "windows-10-error":
          this.canvas.ctx.font = "36px " + this.fonts[this.font].font;
          this.canvas.ctx.fillStyle = this.themes[this.theme].text;
          this.canvas.ctx.fillText(
            ":",
            228,
            this.canvas.height - 160
          );
          this.canvas.ctx.fillText(
            " 0xINIT_ENGINEER",
            228,
            this.canvas.height - 120
          );
          this.canvas.ctx.fillText(
            " https://init.engineer",
            228,
            this.canvas.height - 40
          );
          break;

        // : Windows 10  
        case "windows-10-error-testing":
          this.canvas.ctx.font = "36px " + this.fonts[this.font].font;
          this.canvas.ctx.fillStyle = this.themes[this.theme].text;
          this.canvas.ctx.fillText(
            ":",
            228,
            this.canvas.height - 160
          );
          this.canvas.ctx.fillText(
            " 0xINIT_ENGINEER",
            228,
            this.canvas.height - 120
          );
          this.canvas.ctx.fillText(
            " https://init.engineer",
            228,
            this.canvas.height - 40
          );
          break;

        // : 
        case "broken-think":
          this.canvas.ctx.font = "72px " + this.fonts[this.font].font;
          this.canvas.ctx.fillStyle = this.themes[this.theme].text;
          this.canvas.ctx.fillText("", 360, this.canvas.height - 160);
          this.canvas.ctx.fillText("", 360, this.canvas.height - 80);
          this.canvas.ctx.font = "36px " + this.fonts[this.font].font;
          this.canvas.ctx.fillStyle = this.themes[this.theme].text;
          this.canvas.ctx.fillText(
            "",
            this.canvas.width - 232,
            this.canvas.height - 24
          );
          break;

        // : 
        case "furry-broken-think":
          this.canvas.ctx.font = "72px " + this.fonts[this.font].font;
          this.canvas.ctx.fillStyle = this.themes[this.theme].text;
          this.canvas.ctx.fillText("", 360, this.canvas.height - 160);
          this.canvas.ctx.fillText("", 360, this.canvas.height - 80);
          this.canvas.ctx.font = "36px " + this.canvas.font;
          this.canvas.ctx.fillStyle = this.themes[this.theme].text;
          this.canvas.ctx.fillText(
            "",
            this.canvas.width - 232,
            this.canvas.height - 24
          );
          break;

        //  LOGO 
        default:
          this.canvas.ctx.font = "36px " + this.fonts[this.font].font;
          this.canvas.ctx.fillStyle = this.themes[this.theme].text;
          this.canvas.ctx.fillText(
            "",
            this.canvas.width - 232,
            this.canvas.height - 24
          );
          break;
      }
    },
    /**
     * 
     *
     * @return void
     */
    drawingUrl() {
      switch (this.theme) {
        // : Windows 10 
        case "windows-10-error":
          this.canvas.ctx.font = "192px " + this.fonts[this.font].font;
          this.canvas.ctx.fillStyle = this.themes[this.theme].text;
          this.canvas.ctx.fillText(":(", 48, 192);
          break;

        // : Windows 10  
        case "windows-10-error-testing":
          this.canvas.ctx.font = "192px " + this.fonts[this.font].font;
          this.canvas.ctx.fillStyle = this.themes[this.theme].text;
          this.canvas.ctx.fillText(":(", 48, 192);
          break;

        // 
        default:
          this.canvas.ctx.font = "36px " + this.fonts[this.font].font;
          this.canvas.ctx.fillStyle = this.themes[this.theme].text;
          this.canvas.ctx.fillText(
            " https://init.engineer",
            16,
            this.canvas.height - 24
          );
          break;
      }
    },
    /**
     * 
     *
     * @return void
     */
    drawingContent() {
      /**
       *  array[string]
       *  forEach 
       */
      let contentList = this.contentSplit();
      contentList.forEach(
        function (contentValue, contentKey) {
          /**
           *  x, y 
           */
          let xPoint = 36;
          let yPoint = 0;

          /**
           * 
           * :
           * :
           */
          if (this.canvas.center) {
            yPoint = 440 + ((contentKey - 1) * 80 - contentList.length * 40);
          } else {
            yPoint = 96 + contentKey * 80;
          }

          /**
           * (theme) x, y 
           */
          switch (this.theme) {
            // : Windows 10 
            case "windows-10-error":
              yPoint += 240;
              break;
            // : Windows 10  
            case "windows-10-error-testing":
              yPoint += 240;
              break;
            // : 
            case "broken-think":
              xPoint += 349;
              yPoint += 24;
              break;
            // : 
            case "furry-broken-think":
              xPoint += 349;
              yPoint += 24;
              break;
          }

          /**
           * 
           */
          this.canvas.ctx.font = "63px " + this.fonts[this.font].font;
          this.canvas.ctx.fillStyle = this.themes[this.theme].text;
          this.canvas.ctx.fillText(contentValue, xPoint, yPoint);
        }.bind(this)
      );
    },

    // ...

    /**
     * (array[string])
     *
     * @return array[string]
     */
    contentSplit() {
      /**
       * 
       */
      let responseList = [];
      /**
       * 
       */
      let contentList = this.content.split(/\r\n|\r|\n/);
      contentList.forEach(function (contentValue) {
        /**
         * 
         */
        let content_strlen = encodeURIComponent(contentValue).replace(
          /%[A-F\d]{2}/g,
          "U"
        ).length;
        if (content_strlen <= 42) {
          /**
           *  42
           */
          responseList.push(contentValue);
        } else {
          /**
           *  42
           *
           * conntentWidth => 
           * charStrring => 
           * contentValueList => 
           */
          let contentWidth = 0;
          let charString = "";
          let contentValueList = contentValue.split("");
          /**
           * 
           */
          contentValueList.forEach(function (charValue, charKey) {
            /**
             * 
             */
            let charStrlen = encodeURIComponent(charValue).replace(
              /%[A-F\d]{2}/g,
              "U"
            ).length;
            /**
             *  3  1 0.5
             * 
             */
            contentWidth += charStrlen == 3 ? 1 : 0.5;
            charString += charValue;
            /**
             * 
             */
            if (charKey + 1 in contentValueList) {
              /**
               *  3  1 0.5
               */
              let nextCharStrlen = encodeURIComponent(
                contentValueList[charKey + 1]
              ).replace(/%[A-F\d]{2}/g, "U").length;
              let nextCharWidth = nextCharStrlen == 3 ? 1 : 0.5;
              /**
               * 
               */
              if (contentWidth + nextCharWidth >= 14) {
                /**
                 * 
                 * 
                 */
                responseList.push(charString);
                contentWidth = 0;
                charString = "";
              }
            }
          });

          /**
           * 
           */
          if (charString != "") {
            responseList.push(charString);
          }
        }
      });

      /**
       * 
       */
      return responseList;
    },
    /**
     * 
     *
     * @param array[string] sources 
     * @param function callback 
     *
     * @return void
     */
    loadingImages(sources, callback) {
      var images = {},
        loadedImages = 0,
        numImages = Object.keys(sources).length;
      for (var src in sources) {
        images[src] = new Image();
        images[src].onload = function () {
          if (++loadedImages >= numImages) {
            callback(images);
          }
        };
        images[src].src = sources[src];
      }
    },
  },
  watch: {
    /**
     * 
     */
    content: function (val) {
      this.resetCanvasView();
      this.settingCanvasViewSize();
      this.drawingBackground();
      this.drawingBackgroundImage();
      this.drawingLogo();
      this.drawingUrl();
      this.drawingContent();
    },
    /**
     * 
     */
    theme: function (val) {
      this.resetCanvasView();
      this.settingCanvasViewSize();
      this.drawingBackground();
      this.drawingBackgroundImage();
      this.drawingLogo();
      this.drawingUrl();
      this.drawingContent();
    },
    /**
     * 
     */
    font: function (val) {
      this.resetCanvasView();
      this.settingCanvasViewSize();
      this.drawingBackground();
      this.drawingBackgroundImage();
      this.drawingLogo();
      this.drawingUrl();
      this.drawingContent();
    },
  },
};
</script>

<style scoped>
.preview {
  max-width: 720px;
}
</style>
