<template>
  <div>
    <!--  Logo -->
    <div class="form-group row">
      <label for="companie_logo" class="col-sm-2 col-form-label text-md-right"
        > Logo</label
      >
      <div class="col-sm-10">
        <input
          type="file"
          class="form-control"
          id="companie_logo"
          ref="companie_logo"
          accept="image/jpeg, image/jpg, image/png"
          @change="changeLogo($event)"
        />
        <p class="text-danger mb-0">
          *  512 x 512  2MB 
        </p>
      </div>
    </div>
    <!--form-group-->

    <!--  -->
    <div class="form-group row">
      <label for="companie_name" class="col-sm-2 col-form-label text-md-right"
        ></label
      >
      <div class="col-sm-10">
        <input
          type="text"
          class="form-control"
          id="companie_name"
          placeholder=""
          v-model="name"
        />
        <p class="text-danger mb-0">*</p>
      </div>
    </div>
    <!--form-group-->

    <hr />

    <!--  -->
    <div class="form-group row">
      <label class="col-sm-2 col-form-label text-md-right"></label>
    </div>
    <!--form-group-->

    <!--  -->
    <div class="form-group row">
      <label for="companie_area" class="col-sm-2 col-form-label text-md-right"
        ></label
      >
      <div class="col-sm-10">
        <select class="form-control" id="companie_area" v-model="area">
          <option selected disabled hidden></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </select>
      </div>
    </div>
    <!--form-group-->

    <!--  -->
    <div class="form-group row">
      <label
        for="companie_address"
        class="col-sm-2 col-form-label text-md-right"
        ></label
      >
      <div class="col-sm-10">
        <input
          type="text"
          class="form-control"
          id="companie_address"
          placeholder=" 7 "
          v-model="address"
        />
        <p class="text-danger mb-0">*</p>
      </div>
    </div>
    <!--form-group-->

    <!--  -->
    <div class="form-group row">
      <label for="companie_scale" class="col-sm-2 col-form-label text-md-right"
        ></label
      >
      <div class="col-sm-10">
        <div class="input-group mb-3">
          <div class="input-group-prepend">
            <span class="input-group-text"></span>
          </div>
          <input
            type="number"
            class="form-control"
            id="companie_scale"
            placeholder="10"
            v-model="scale"
          />
          <div class="input-group-append">
            <span class="input-group-text"></span>
          </div>
        </div>
      </div>
    </div>
    <!--form-group-->

    <!--  -->
    <div class="form-group row">
      <label for="companie_tax" class="col-sm-2 col-form-label text-md-right"
        ></label
      >
      <div class="col-sm-10">
        <input
          type="number"
          class="form-control"
          id="companie_tax"
          placeholder="12345678"
          v-model="tax"
        />
      </div>
    </div>
    <!--form-group-->

    <!--  -->
    <div class="form-group row">
      <label
        for="companie_capital"
        class="col-sm-2 col-form-label text-md-right"
        ></label
      >
      <div class="col-sm-10">
        <div class="input-group mb-3">
          <div class="input-group-prepend">
            <span class="input-group-text">$</span>
          </div>
          <input
            type="number"
            class="form-control"
            id="companie_capital"
            placeholder="10"
            v-model="capital"
          />
          <div class="input-group-append">
            <span class="input-group-text"></span>
          </div>
        </div>
      </div>
    </div>
    <!--form-group-->

    <hr />

    <!--  -->
    <div class="form-group row">
      <label class="col-sm-2 col-form-label text-md-right"></label>
    </div>
    <!--form-group-->

    <!--  -->
    <div class="form-group row">
      <label for="companie_email" class="col-sm-2 col-form-label text-md-right"
        ></label
      >
      <div class="col-sm-10">
        <input
          type="text"
          class="form-control"
          id="companie_email"
          placeholder="hr@example.com"
          v-model="email"
        />
        <p class="text-danger mb-0">*</p>
      </div>
    </div>
    <!--form-group-->

    <!--  -->
    <div class="form-group row">
      <label for="companie_photo" class="col-sm-2 col-form-label text-md-right"
        ></label
      >
      <div class="col-sm-10">
        <input
          type="text"
          class="form-control"
          id="companie_photo"
          placeholder="(09)11-111-111"
          v-model="phone"
        />
      </div>
    </div>
    <!--form-group-->

    <hr />

    <!--  -->
    <div class="form-group row">
      <label
        for="companie_description"
        class="col-sm-2 col-form-label text-md-right"
        ></label
      >
      <div class="col-sm-10">
        <textarea
          type="text"
          id="companie_description"
          class="form-control"
          rows="4"
          maxlength="2000"
          v-model="description"
          placeholder=" ..."
        ></textarea>
      </div>
    </div>
    <!--form-group-->

    <!--  -->
    <div class="form-group row">
      <label class="col-sm-2 col-form-label text-md-right"></label>
      <div class="col-sm-10">
        <p class="p-2" v-show="contents.length === 0">
          
        </p>
      </div>
    </div>
    <!--form-group-->

    <!--  -->
    <div
      class="form-group row"
      v-for="(content, index) in contents"
      v-bind:key="index"
    >
      <div class="col-sm-2">
        <input
          type="text"
          class="form-control"
          placeholder="..."
          v-model="content.key"
          :for="content.key"
          :id="content.key"
        />
        <button
          type="button"
          class="btn btn-sm btn-danger mt-2"
          @click="destroyContent(index)"
        >
          
        </button>
      </div>
      <div class="col-sm-10">
        <textarea
          type="text"
          class="form-control"
          rows="4"
          maxlength="2000"
          v-model="content.value"
          :id="content.key"
          :placeholder="content.key + ' ...'"
        ></textarea>
      </div>
    </div>
    <!--form-group-->

    <!--  -->
    <div class="form-group row">
      <div class="col-sm-8 offset-sm-2">
        <button
          type="button"
          class="btn btn-lg btn-primary"
          @click="createContent()"
        >
          
        </button>
        <p class="text-danger mb-0">
          * 10  2,000
          
        </p>
      </div>
    </div>
    <!--form-group-->

    <hr />

    <!--  -->
    <div class="form-group row">
      <label class="col-sm-2 col-form-label text-md-right"></label>
    </div>
    <!--form-group-->

    <!--  -->
    <div class="form-group row">
      <label for="companie_banner" class="col-sm-2 col-form-label text-md-right"
        ></label
      >
      <div class="col-sm-10">
        <input
          type="file"
          class="form-control"
          id="companie_banner"
          ref="companie_banner"
          accept="image/jpeg, image/jpg, image/png"
          @change="changeBanner($event)"
        />
        <p class="text-danger mb-0">
          *  1280 x 720  2MB 
        </p>
      </div>
    </div>
    <!--form-group-->

    <!--  -->
    <div class="form-group row">
      <label
        for="companie_picture"
        class="col-sm-2 col-form-label text-md-right"
        ></label
      >
      <div class="col-sm-10">
        <div
          class="input-group mb-3"
          v-for="(picture, index) in pictures"
          v-bind:key="index"
        >
          <input
            type="file"
            class="form-control"
            name="companie_picture[]"
            :id="`companie_picture_${index}`"
            :ref="`companie_picture_${index}`"
            accept="image/jpeg, image/jpg, image/png"
            @change="changePicture($event, index)"
          />
          <div class="input-group-append">
            <button
              type="button"
              class="btn btn-outline-danger"
              @click="destroyPicture(index)"
            >
              
            </button>
          </div>
        </div>

        <p class="p-2" v-show="pictures.length === 0">
          
        </p>
      </div>
    </div>
    <!--form-group-->

    <!--  -->
    <div class="form-group row">
      <div class="col-sm-8 offset-sm-2">
        <button
          type="button"
          class="btn btn-lg btn-primary"
          @click="createPicture()"
        >
          
        </button>
        <p class="text-danger mb-0">
          * 10  2MB
        </p>
      </div>
    </div>
    <!--form-group-->

    <hr />

    <!--  -->
    <div class="form-group row">
      <div class="col-sm-4 offset-sm-4">
        <button
          type="button"
          class="btn btn-lg btn-block btn-info mt-2"
          @click="createCompanie()"
        >
          <div v-if="status === null"></div>
          <div v-else>
            <span
              class="spinner-border spinner-border-sm"
              role="status"
              aria-hidden="true"
            ></span>
            <span class="sr-only">Loading...</span>
          </div>
        </button>
      </div>
    </div>
    <!--form-group-->
  </div>
</template>

<script>
export default {
  name: "CreateCompanie",
  data() {
    return {
      logo: null,
      banner: null,
      pictures: [],
      name: null,
      area: null,
      address: null,
      scale: null,
      tax: null,
      capital: null,
      email: null,
      phone: null,
      description: null,
      contents: [
        {
          key: "",
          value: null,
        },
        {
          key: "",
          value: null,
        },
        {
          key: "",
          value: null,
        },
      ],
      status: null,
    };
  },
  methods: {
    /**
     * 
     *
     * @return void
     */
    createContent() {
      this.contents.push({
        key: null,
        value: null,
      });
    },
    /**
     * 
     *
     * @param int index
     *
     * @return void
     */
    destroyContent(index) {
      this.contents.splice(index, 1);
    },
    /**
     * 
     *
     * @return void
     */
    createPicture() {
      this.pictures.push(null);
    },
    /**
     * 
     *
     * @param int index
     *
     * @return void
     */
    destroyPicture(index) {
      this.pictures.splice(index, 1);
    },
    /**
     * LOGO 
     *
     * @param elements event
     *
     * @return void
     */
    changeLogo(event) {
      if (event.target.files && event.target.files[0]) {
        let uploaded = event.target.files[0];
        if (
          this.checkImage(event.target, uploaded, {
            height: 512,
            width: 512,
          })
        ) {
          this.logo = uploaded;

          return;
        }
      }

      this.$refs.companie_logo.value = null;
    },
    /**
     * 
     *
     * @param elements event
     *
     * @return void
     */
    changeBanner(event) {
      if (event.target.files && event.target.files[0]) {
        let uploaded = event.target.files[0];
        if (
          this.checkImage(event.target, uploaded, {
            height: 720,
            width: 1280,
          })
        ) {
          this.banner = uploaded;

          return;
        }
      }

      this.$refs.companie_banner.value = null;
    },
    /**
     * 
     *
     * @param elements event
     * @param int index
     *
     * @return void
     */
    changePicture(event, index) {
      if (event.target.files && event.target.files[0]) {
        let uploaded = event.target.files[0];
        if (this.checkImage(event.target, uploaded)) {
          this.pictures[index] = uploaded;

          return;
        }
      }
    },
    /**
     * 
     *
     * @param Image file
     * @param Object limit
     *
     * @return Image
     */
    checkImage(
      target,
      file,
      limit = {
        height: null,
        width: null,
      }
    ) {
      // 
      let accepted = ["jpg", "jpeg", "png"];
      if (!new RegExp(accepted.join("|")).test(file.type)) {
        Swal.fire(
          "",
          ' <strong>"jpg""jpeg""png"</strong> ',
          "error"
        );
        return false;
      }

      // 
      if (file.size >= 2 * 1024 * 1024) {
        Swal.fire(
          "",
          " <strong>2MB</strong> ",
          "error"
        );
        return false;
      }

      // 
      if (limit.height !== null && limit.width !== null) {
        let reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = (evt) => {
          let img = new Image();
          img.onload = () => {
            if (limit.height !== img.height || limit.width !== img.width) {
              Swal.fire(
                "",
                `<p><strong>(Height) ${limit.height}  (Width) ${limit.width}</strong></p><p><strong>(Height) ${img.height}  (Width) ${img.width}</strong></p>`,
                "error"
              );
              target.value = null;

              return false;
            }
          };
          img.src = evt.target.result;
        };
      }

      // 
      return file;
    },
    /**
     * 
     */
    createCompanie() {
      /**
       * 
       */
      if (this.name === null || this.name === "") {
        Swal.fire(
          "",
          "<p><strong></strong></p>",
          "error"
        );

        return;
      }

      /**
       * 
       */
      if (this.area === null || this.area === "") {
        Swal.fire(
          "",
          "",
          "error"
        );

        return;
      }

      /**
       * 
       */
      if (this.address === null || this.address === "") {
        Swal.fire(
          "",
          "<p><strong> 7 </strong></p>",
          "error"
        );

        return;
      }

      /**
       * 
       */
      if (this.email === null || this.email === "") {
        Swal.fire(
          "",
          "<p><strong>hr@example.com</strong></p>",
          "error"
        );

        return;
      }

      this.status = "create";

      /**
       *  FormData
       */
      let data = new FormData();
      data.append("name", this.name);
      data.append("area", this.area);
      data.append("address", this.address);
      data.append("email", this.email);

      /**
       *  FormData
       */
      if (this.scale !== null) {
        data.append("scale", this.scale);
      }

      if (this.tax !== null) {
        data.append("tax", this.tax);
      }

      if (this.capital !== null) {
        data.append("capital", this.capital);
      }

      if (this.phone !== null) {
        data.append("phone", this.phone);
      }

      if (this.description !== null) {
        data.append("description", this.description);
      }

      if (this.contents !== null) {
        this.contents.forEach((element) => {
          data.append("contents[" + element.key + "]", element.value);
        });
      }

      /**
       *  LOGO File 
       */
      if (this.logo !== null) {
        data.append("logo", this.logo);
      }

      /**
       *  Banner File 
       */
      if (this.banner !== null) {
        data.append("banner", this.banner);
      }

      /**
       *  Pictures File 
       */
      if (this.pictures.length !== 0) {
        let i = 0;
        this.pictures.forEach((element) => {
          if (element !== null) {
            data.append("pictures[" + i + "]", element);
            i = i + 1;
          }
        });
      }

      /**
       *  Companie 
       */
      let self = this;
      axios({
        method: "post",
        url: "/api/companies",
        data: data,
        headers: {
          "Content-Type": "multipart/form-data",
        },
      })
        .then(function (response) {
          self.status = null;
          document.location.href = `/companie/${response.data.data.uuid}`;
        })
        .catch(function (error) {
          /**
           * 
           */
          self.status = null;
          Swal.fire(
            "",
            '<a href="https://github.com/init-engineer/init.engineer"> GitHub repo </a> issue  m(_ _)m',
            "error"
          );
        });
    },
  },
};
</script>
