<template>
  <div class="multi-step-form" style="max-width: 100vw">
    <div class="multi-form py-3 mx-3">
      <div class="inner">
        <ul class="steps p-0" @click="onClickListener($event)">
          <!-- Step 1 -  Start -->
          <li ref="listItem1" class="listItem show" id="step1">
            <div class="col1">
              <span class="step">
                <span>1</span>
                <span class="checkmark"></span>
              </span>
              <span class="line"></span>
            </div>
            <div ref="stepBody" class="col2 stepBody">
              <div class="stepTitle">Step 1 - </div>
              <div class="content">
                <div
                  class="mw-100 my-2 mx-2"
                  style="
                    height: 600px;
                    border: 4px dashed var(--color-success);
                    border-radius: 24px;
                    cursor: pointer;
                  "
                  v-if="picture !== null"
                  @click="$refs.picturePicker.click()"
                >
                  <img
                    class="rounded mx-auto d-block h-100"
                    ref="previewPicture"
                  />
                </div>
                <div
                  :class="{ 'image-upload-wrap my-2 mx-2': picture === null }"
                  @click="$refs.picturePicker.click()"
                >
                  <input
                    type="file"
                    id="picturePicker"
                    ref="picturePicker"
                    accept="image/*"
                    @change="pictureUpload($event)"
                  />
                  <div
                    class="text-center text-success py-5 px-2"
                    v-if="picture === null"
                  >
                    <h3></h3>
                  </div>
                </div>
                <p class="text-danger m-0 pb-0">
                  
                  <strong>"jpg""jpeg""png""gif"</strong> 
                </p>
                <p class="text-danger m-0 pt-0 pb-2">
                   <strong>10MB</strong> 
                </p>
                <p
                  v-if="picture !== null"
                  v-text="
                    `${picture.name}`
                  "
                ></p>
                <div class="buttons">
                  <button class="next" :disabled="picture === null">
                    
                  </button>
                </div>
              </div>
            </div>
          </li>
          <!-- Step 1 -  End -->

          <!-- Step 2 -  Start -->
          <li ref="listItem2" class="listItem" id="step2">
            <div class="col1">
              <span class="step">
                <span>2</span>
                <span class="checkmark"></span>
              </span>
              <span class="line"></span>
            </div>
            <div ref="stepBody" class="col2 stepBody">
              <div class="stepTitle">Step 2 - </div>
              <div class="content">
                <div class="inputGroup">
                  <label for="name"></label>
                  <textarea
                    class="form-control cards-editor"
                    rows="8"
                    minlength="30"
                    maxlength="4096"
                    v-model="content"
                    required
                    @keyup="onKeyupListener($event)"
                  ></textarea>
                </div>
                <div class="buttons pt-2">
                  <button class="next" disabled></button>
                  <button class="prev"></button>
                </div>
              </div>
            </div>
          </li>
          <!-- Step 2 -  End -->

          <!-- Step 3 -  Start -->
          <li ref="listItem3" class="listItem" id="step3">
            <div class="col1">
              <span class="step">
                <span>3</span>
                <span class="checkmark"></span>
              </span>
              <span class="line"></span>
            </div>
            <div ref="stepBody" class="col2 stepBody">
              <div class="stepTitle">Step 3 - </div>
              <div class="jumbotron jumbotron-fluid rounded p-2 mt-2">
                <div class="container p-0">
                  <h1 class="text-center w-100 py-2 my-0"></h1>
                  <h3></h3>
                  <p>
                    
                  </p>
                  <h3></h3>
                  <ol class="pb-2">
                    <li></li>
                    <li></li>
                    <li>
                      
                    </li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li>
                      
                    </li>
                    <li>
                      
                    </li>
                    <li></li>
                    <li></li>
                  </ol>
                  <h3></h3>
                  <p>
                    
                  </p>
                  <h3></h3>
                  <ol class="pb-2">
                    <li>
                      
                    </li>
                    <li></li>
                    <li>
                      
                    </li>
                    
                  </ol>
                </div>
                <div class="mt-2 text-center">
                  <input
                    class="tgl tgl-flip"
                    id="consent"
                    type="checkbox"
                    v-model="consent"
                  />
                  <label
                    style="display: inline-block"
                    class="tgl-btn mt-2 mr-2"
                    data-tg-off=""
                    data-tg-on=""
                    for="consent"
                  ></label>
                  <label
                    style="display: inline-block"
                    :class="[consent ? 'text-success' : 'text-danger']"
                    for="consent"
                  >
                    {{
                      consent
                        ? ""
                        : ""
                    }}
                  </label>
                </div>
                <div class="buttons pt-2">
                  <button class="next" :disabled="!consent"></button>
                  <button
                    class="prev"
                    style="background-color: var(--background-secondary)"
                  >
                    
                  </button>
                </div>
              </div>
            </div>
          </li>
          <!-- Step 3 -  End -->

          <!-- Step 4 -  Start -->
          <li ref="listItem4" class="listItem" id="step4">
            <div class="col1">
              <span class="step">
                <span>4</span>
                <span class="checkmark"></span>
              </span>
              <span class="line"></span>
            </div>
            <div ref="stepBody" class="col2 stepBody">
              <div class="stepTitle">Step 4 - </div>
              <div class="content">
                <div class="pt-2" v-if="final !== 'success'">
                  <div class="row mb-5" style="max-height: 1200px">
                    <div class="col-md-6">
                      <img
                        class="rounded mx-auto d-block w-100"
                        style="max-height: 600px"
                        :src="pictureSrc"
                      />
                    </div>
                    <div class="col-md-6">
                      <h3 v-if="picture !== null">
                        {{ picture.name }}
                      </h3>
                      <p style="font-size: 24px">{{ content }}</p>
                    </div>
                  </div>
                  <h3 class="w-100">
                    
                  </h3>
                  <div class="buttons">
                    <button
                      type="button"
                      class="submit btn yes"
                      @click="submitForm()"
                      :disabled="final !== null"
                    >
                      <div v-if="final === null"></div>
                      <div v-else>
                        <span
                          class="spinner-border spinner-border-sm"
                          role="status"
                          aria-hidden="true"
                        ></span>
                        <span class="sr-only">Loading...</span>
                      </div>
                    </button>
                    <button class="prev" v-if="final === null"></button>
                  </div>
                </div>
                <div v-if="final === 'success'">
                  <h3 class="success"></h3>
                  <p>
                    
                  </p>
                </div>
              </div>
            </div>
          </li>
          <!-- Step 4 -  End -->

          <!-- Step 5 -  Start -->
          <li ref="listItem5" class="listItem" id="step5">
            <div class="col1">
              <span class="step">
                <span>5</span>
                <span class="checkmark"></span>
              </span>
              <span class="line"></span>
            </div>
            <div ref="stepBody" class="col2 stepBody">
              <div class="stepTitle">Step 5 - </div>
              <div class="content">
                <div class="col-md-8 offset-md-2 mb-3" id="flowchart">
                  <div class="row">
                    <ul class="col-12">
                      <li class="flow-step flow-light"></li>
                      <li class="flow-arrow">
                        <i class="fas fa-arrow-down"></i>
                      </li>
                      <li class="flow-step flow-light">
                        <p class="mb-0"></p>
                        <h5>
                          <span class="badge badge-pill badge-secondary"
                            ></span
                          >
                        </h5>
                      </li>
                      <li class="flow-arrow">
                        <i class="fas fa-arrow-down"></i>
                      </li>
                      <li class="flow-step flow-light">
                        <p class="mb-0"></p>
                        <a href="/cards/review" class="p-0">
                          <h5>
                            <span class="badge badge-pill badge-primary"
                              ></span
                            >
                          </h5>
                        </a>
                      </li>
                    </ul>
                    <ul class="col-4">
                      <li class="flow-arrow text-success">
                        <i class="fas fa-arrow-down text-success mr-2"></i>
                        
                      </li>
                      <li class="flow-step flow-success">
                        <p class="m-0"></p>
                        <p class="m-0"></p>
                      </li>
                    </ul>
                    <ul class="col-8">
                      <li class="flow-arrow text-warning">
                        <i class="fas fa-arrow-down text-warning mr-2"></i>
                        
                      </li>
                      <li class="flow-step flow-warning">
                        <p class="m-0"></p>
                        <p class="m-0"></p>
                      </li>
                    </ul>
                    <ul class="col-2">
                      <!--  -->
                    </ul>
                    <ul class="col-5">
                      <li class="flow-arrow text-success">
                        
                        <i class="fas fa-arrow-down text-success ml-2"></i>
                      </li>
                      <li class="flow-step flow-success">
                        <p class="m-0"></p>
                        <p class="m-0"></p>
                      </li>
                    </ul>
                    <ul class="col-5">
                      <li class="flow-arrow text-danger">
                        <i class="fas fa-arrow-down text-danger mr-2"></i>
                        
                      </li>
                      <li class="flow-step flow-danger">
                        <p class="m-0"></p>
                        <p class="m-0"></p>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </li>
          <!-- Step 5 -  End -->
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "PublishArticle",
  data() {
    return {
      stepsWrapper: null,
      listItems: null,
      inputs: null,
      content: "",
      consent: false,
      final: null,
      picture: null,
      pictureSrc: null,
    };
  },
  mounted() {
    this.stepsWrapper = document.querySelector(".steps");
    this.listItems = this.stepsWrapper.querySelectorAll(".listItem");
    this.inputs = this.stepsWrapper.querySelectorAll("input");

    //  Step 1
    this.changeSteps(this.$refs.listItem1, this.$refs.listItem1);
  },
  methods: {
    /**
     * 
     *
     * @param elements event
     *
     * @return void
     */
    pictureUpload(event) {
      // 
      if (event.target.files && event.target.files[0]) {
        let uploaded = event.target.files[0];

        // 
        let accepted = ["jpg", "jpeg", "png", "gif"];
        if (!new RegExp(accepted.join("|")).test(uploaded.type)) {
          Swal.fire(
            "",
            ' <strong>"jpg""jpeg""png""gif"</strong> ',
            "error"
          );
          return;
        }

        // 
        if (uploaded.size >= 10 * 1024 * 1024) {
          Swal.fire(
            "",
            " <strong>10MB</strong> ",
            "error"
          );
          return;
        }

        // 
        this.picture = uploaded;
        let reader = new FileReader();
        let self = this;
        reader.onload = function () {
          self.$refs.previewPicture.src = reader.result;
          self.pictureSrc = reader.result;
          self.changeSteps(self.$refs.listItem1, self.$refs.listItem1);
        };
        reader.readAsDataURL(self.picture);

        return;
      }

      // 
      Swal.fire(
        "",
        '<a href="https://github.com/init-engineer/init.engineer"> GitHub repo </a> issue  Debug',
        "error"
      );
      return;
    },
    /**
     * 
     *
     * @param elements event
     *
     * @return void
     */
    onKeyupListener(event) {
      //  keyup  input 
      const inputWrapper = event.target.closest(".inputGroup");
      const nextButton = inputWrapper
        .closest(".stepBody")
        .querySelector(".next");

      // 
      if (this.consent) {
        return (nextButton.disabled = false);
      }

      //  value return
      if (!event.target.value) {
        inputWrapper.closest(".listItem").classList.remove("done");
        inputWrapper.classList.remove("js-focus");
        return (nextButton.disabled = true);
      }
      inputWrapper.classList.add("js-focus");

      // 
      if (
        inputWrapper.nextElementSibling.classList.contains("inputGroup") &&
        !inputWrapper.nextElementSibling.querySelector("input").value
      ) {
        return;
      }

      //  30 
      if (this.content.length < 30) {
        return;
      }

      // 
      nextButton.disabled = false;
    },
    /**
     * 
     *
     * @param elements event
     *
     * @return void
     */
    onClickListener(event) {
      // 
      if (!event.target.closest("button")) {
        return;
      }

      const btn = event.target;
      const currentStep = btn.closest(".listItem");

      btn.classList.contains("next")
        ? this.changeSteps(currentStep, currentStep.nextElementSibling)
        : btn.classList.contains("prev")
        ? this.changeSteps(currentStep, currentStep.previousElementSibling)
        : null;
    },
    /**
     * 
     *
     * @return void
     */
    submitForm() {
      // 
      this.final = "submit";

      //  API
      let self = this;
      let formData = new FormData();
      formData.append("content", self.content);
      formData.append("picture", self.picture);
      axios
        .post(`/api/social/cards/publish/picture`, formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((response) => {
          // 
          self.final = "success";
          self.changeSteps(self.$refs.listItem4, self.$refs.listItem5);

          // 
          let id = response.data.data.id;
          Swal.fire(
            "",
            `<a href="/cards/show/${id}"> ${id}(#${id.toString(
              36
            )}) </a><a href="/cards/review"></a>`,
            "success"
          );
        })
        .catch((error) => {
          //  Error 
          self.final = null;
          Swal.fire(
            "",
            '<a href="https://github.com/init-engineer/init.engineer"> GitHub repo </a> issue  m(_ _)m',
            "error"
          );
        });
    },
    /**
     *  Step 
     *
     * @param elements currentStep
     * @param elements newStep
     *
     * @return void
     */
    changeSteps(currentStep, newStep) {
      //  Step  Step 
      currentStep.style.height = getComputedStyle(newStep).height;
      currentStep.classList.remove("show");

      // 
      if (newStep === currentStep.nextElementSibling)
        currentStep.classList.add("done");

      // 
      const contentHeight = newStep
        .querySelector(".stepBody")
        .getBoundingClientRect().height;
      newStep.style.height = `${contentHeight}px`;
      newStep.classList.add("show");

      // 
      this.scrollTo(newStep.id);
    },
    /**
     *  Step
     *
     * @param string id  ID 
     *
     * @return void
     */
    scrollTo(id) {
      //  alert 
      let alert = document.getElementsByClassName("alert");
      let alertHeight = 48;
      for (var i = 0; i < alert.length; i++) {
        alertHeight = alertHeight + alert[i].getBoundingClientRect().height;
      }

      //  Step 
      let y = 0;
      switch (id) {
        case "step1":
          y = 0;
          break;
        case "step2":
          y = 66;
          break;
        case "step3":
          y = 132;
          break;
        case "step4":
          y = 198;
          break;
        case "step5":
          y = 264;
          break;
      }

      // 
      window.scrollTo({ top: alertHeight + y, behavior: "smooth" });
    },
  },
};
</script>
