<template>
  <div class="d-flex justify-content-center">
    <!--  -->
    <div v-if="states === 'loading'" class="spinner-border" role="states">
      <span class="sr-only">Loading...</span>
    </div>

    <!--  -->
    <div
      v-else-if="states === 'vote' || states === 'voting'"
      style="position: relative; width: 130px"
    >
      <button
        type="button"
        class="btn yes"
        @click="yesVoting()"
        :disabled="states === 'voting'"
      >
        <div v-if="voting !== 'yes'"></div>
        <div v-else>
          <span
            class="spinner-border spinner-border-sm"
            role="status"
            aria-hidden="true"
          ></span>
          <span class="sr-only">Loading...</span>
        </div>
      </button>

      <button
        type="button"
        class="btn no"
        @click="noVoting()"
        :disabled="states === 'voting'"
      >
        <div v-if="voting !== 'no'"></div>
        <div v-else>
          <span
            class="spinner-border spinner-border-sm"
            role="status"
            aria-hidden="true"
          ></span>
          <span class="sr-only">Loading...</span>
        </div>
      </button>
    </div>

    <!--  -->
    <div v-else-if="states === 'complete'">
      <div class="clearfix">
        <p
          class="float-left mb-0"
          style="color: rgb(26, 188, 156)"
          v-show="voting === 'yes'"
        >
          
        </p>
        <p
          class="float-right mb-0"
          style="color: rgb(216, 73, 90)"
          v-show="voting === 'no'"
        >
          
        </p>
      </div>
      <div
        class="progress"
        style="position: relative; width: 130px; height: 48px"
      >
        <div
          class="progress-bar progress-bar-striped progress-bar-animated"
          role="progressbar"
          :style="
            'width: ' +
            ((count.yes / (count.yes + count.no)) * 100).toFixed(2) +
            '%; border-style: solid none solid solid; background-color: rgb(26, 188, 156);'
          "
          :aria-valuenow="count.yes"
          :aria-valuemin="0"
          :aria-valuemax="count.yes + count.no"
        >
          {{ ((count.yes / (count.yes + count.no)) * 100).toFixed(2) }}%
        </div>
        <div
          class="progress-bar progress-bar-striped progress-bar-animated"
          role="progressbar"
          :style="
            'width: ' +
            ((count.no / (count.yes + count.no)) * 100).toFixed(2) +
            '%; border-style: solid solid solid none; background-color: rgb(216, 73, 90);'
          "
          :aria-valuenow="count.no"
          :aria-valuemin="0"
          :aria-valuemax="count.yes + count.no"
        >
          {{ ((count.no / (count.yes + count.no)) * 100).toFixed(2) }}%
        </div>
      </div>
      <div class="clearfix">
        <p class="float-left" style="color: rgb(26, 188, 156)">
          {{ count.yes }}
        </p>
        <p class="float-right" style="color: rgb(216, 73, 90)">
          {{ count.no }}
        </p>
      </div>
    </div>

    <!--  -->
    <div v-else>
      <h1><span class="badge badge-secondary">?</span></h1>
    </div>
  </div>
</template>

<script>
export default {
  name: "ReviewButton",
  props: {
    /**
     * 
     */
    cid: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      /**
       * 
       */
      states: "loading",

      /**
       * 
       */
      voting: null,

      /**
       * 
       */
      count: {
        yes: 0,
        no: 0,
      },
    };
  },
  mounted() {
    /**
     * 
     */
    let self = this;
    axios
      .get(`/api/social/cards/${this.cid}/voted`)
      .then(function (response) {
        if (response.data.voted) {
          /**
           * 
           */
          self.count.yes = response.data.count.yes;
          self.count.no = response.data.count.no;
          self.voting = response.data.selector ? "yes" : "no";
          self.states = "complete";
        } else {
          /**
           *  false 
           */
          self.states = "vote";
        }
      })
      .catch(function (error) {
        /**
         * 
         */
        self.states = "error";
        Swal.fire(
          "",
          '<a href="https://github.com/init-engineer/init.engineer"> GitHub repo </a> issue  m(_ _)m',
          "error"
        );
      });
  },
  methods: {
    /**
     * 
     *
     * @return void
     */
    yesVoting() {
      this.states = "voting";
      this.voting = "yes";

      let self = this;
      axios
        .get(`/api/social/cards/${this.cid}/voting/1`)
        .then(function (response) {
          self.count.yes = response.data.count.yes;
          self.count.no = response.data.count.no;
          self.states = "complete";
        })
        .catch(function (error) {
          /**
           * 
           */
          self.states = "vote";
          Swal.fire(
            "",
            '<a href="https://github.com/init-engineer/init.engineer"> GitHub repo </a> issue  m(_ _)m',
            "error"
          );
        });
    },
    /**
     * 
     *
     * @return void
     */
    noVoting() {
      this.states = "voting";
      this.voting = "no";

      let self = this;
      axios
        .get(`/api/social/cards/${this.cid}/voting/0`)
        .then(function (response) {
          self.count.yes = response.data.count.yes;
          self.count.no = response.data.count.no;
          self.states = "complete";
        })
        .catch(function (error) {
          /**
           * 
           */
          self.states = "vote";
          Swal.fire(
            "",
            '<a href="https://github.com/init-engineer/init.engineer"> GitHub repo </a> issue  m(_ _)m',
            "error"
          );
        });
    },
  },
};
</script>

<style scoped>
.btn {
  display: inline-block;
  margin: 0px;
  padding: 2px;
  width: 60px;
  height: 48px;
  border-radius: 4px;
  color: #ffffff;
  position: relative;
  cursor: pointer;
}
.btn.yes {
  background-color: rgb(26, 188, 156);
  margin-right: 6px;
}
.btn.yes:hover {
  background-color: rgb(29, 200, 166);
}
.btn.no {
  background-color: rgb(216, 73, 90);
}
.btn.no:hover {
  background-color: rgb(231, 79, 97);
}
</style>
